.img-wrapper{
    padding: 6px 0px 0px 0px;
    width: 100%;
}
img{
    width: 100%;
}

head{
    display:block;
}

.form-control {
    width: 100%; 
    height: 100%;
    padding: 10px;
    background-color: #ffffff00;
    border-radius: 3px;
    box-shadow: 2px 2px 7px #d9d9d9;
        position: relative;
}
.register-container .form-control .head{
