    box-shadow: 2px 2px 5px #d3d3d3;
    letter-spacing: 2px;
    border-radius: 3px;
    padding:5px 10px;
    position: relative;
    text-shadow:none;
}

.form-control .update{
    width:70%;
}

h1{
    font-size:26px;
}

.message{
    position: absolute;
    top: 10%;
    display:none;
    color: red;
    padding:5px;
    width: 100%;
}

.redirect{
    top: 20%;
}

.form-control form {
    margin: auto;
    margin-top: 30px;
}

.form-control form.login-form {
    margin-top:80px;
}

form label {
    font-size: 11px;
    font-weight: 550;
    color: rgb(82, 81, 81);
}

.form-group{
    display: flex;
    background-color: #fff;
    border-radius: 5px;
    padding: 5px;
    box-shadow: inset 1px 1px 5px rgb(177, 177, 177);
}

.form-group div{
    padding: 0 5px;
    font-size: 22px;
    color: rgb(78, 78, 78);
}


.form-group .fa{
    transition: 0.3s ease-in-out;
}


.form-group:hover .fa{
    color: rgb(92, 69, 148);

    transform: scale(1.1);
}

.form-group span{
    display: block;
    width: 2.5px;
    height: 20px;
    margin: auto 3px;
    color: rgb(78, 78, 78);
    background-color: rgb(78, 78, 78);
    transition: 0.3s ease-in-out;
}


.form-group:hover span{
    height: 30px;
}

.form-group input{
    width: 100%;
    font-size: 13px;
    letter-spacing:1px;
    font-weight:600;
    border: none;
    outline: none;
    padding: 5px;
    border-radius: 5px;
    color: rgb(105, 99, 99);
    transition: 0.3s ease-in-out;
}

.form-group input:focus{
    box-shadow: inset 1px 1px 5px rgb(177, 177, 177);

}

.form-group input[type="radio"]{
    width: 70%;
    display:none;
}
.genderMale input:checked + label .fa{
    color: rgb(78, 54, 184);
    transform: scale(1.3);
}

.genderFemale input:checked + label .fa{
    color: rgb(78, 54, 184);
    transform: scale(1.3);

}


.gender{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:30%;
}

button {
    text-align: center;
    align-items: center;
    padding: 12px 10px;
    width: 60%;
    border: none;
    outline: none;
    color: #e8e8e8;
    font-size: 15px;
    font-weight: 700;
    background:none;
    letter-spacing: 2px;
    background-color: hsla(199, 17%, 47%, 0.842);
    box-shadow: 3px 3px 12px rgb(179, 179, 179);
    border-radius: 3px;
    margin: 25px 20%;
