}


button:hover{
    background-color: hsla(200, 16%, 29%, 0.71);
    box-shadow: 8px 8px 15px #adadad, 
    -8px -8px 15px #ffffff;
}

.or-line {
    border-bottom: 2px solid rgb(206, 203, 203);
    text-align: center;
    width: 100%;
    height: 8px;
    margin: 10px 0px 10px 0px;
    position: relative;
    cursor: auto;
}

.or-line span {
    position: absolute;
