    background-color: white;
    font-weight: 550;
    font-size: 10px;
    color: grey;
}

.thirdPartyAuth{
    display:flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.thirdPartyAuth h3{
    font-size: 15px;
    margin-top: 5%;
    color: rgb(73, 69, 69);
    font-size:13px;
    font-weight: 600;
}

.thirdParties{
    width:100%;
    height: 50px;
    margin-top: 5%;
    display: flex;
    justify-content:space-between;
}
.thirdParties>div{
    width: 80px;
    height: 50px;
    padding:14px 27px;
    background-color: #fff;
    border: 2px solid #f6f5fa;
    transition: all .3s;
}

.thirdParties>div:hover{
    border-color:#dddce0;
    box-shadow:0 4px 15px rgba(100,100,102,.6);
}

.thirdParties img{
    margin: 0;
    width: 100%;
}
form a {
    font-size: 15px;
    font-weight: 500;
    text-decoration: none;
    letter-spacing:1px;
}

span{
    color:#494949;
}
.login form{
    margin-top:120px;
}

.register-container{
    width: 930px;
    height: 800px;
}

.register-container .img-wrapper{
    padding: 6px 0px 0px 0px;
    width: 100%;
}
.register-container img{
    width: 100%;
    transform: scale(1.07)
}

