* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
    background-color: rgb(255, 255, 255);
    /* background-color: #B7FFEE; */
}


.container {
    width: 850px;
    height:684px;
    display: flex;
    justify-content:space-between;
    align-items: center;
    border-radius: 5px;
    margin: 8% auto;
    background-color:#ffffffeb;
    padding:0;
    box-shadow: 5px 5px 20px #c9c5c500,-5px -5px 20px #a2a2a242;
}

.img-wrapper{
    padding: 6px 0px 0px 0px;
    width: 100%;
}
img{
    width: 100%;
}

head{
    display:block;
}

.form-control {
    width: 100%; 
    height: 100%;
    padding: 10px;
    background-color: #ffffff00;
    border-radius: 3px;
    box-shadow: 2px 2px 7px #d9d9d9;
        position: relative;
}
.register-container .form-control .head{
    width: 50%;
    font-weight:700;
    color: rgba(56, 34, 252, 0.671);
    box-shadow: 2px 2px 5px #d3d3d3;
    letter-spacing: 2px;
    border-radius: 3px;
    padding:5px 10px;
    text-shadow:none;
}

.login .form-control .head{
    width: 50%;
    font-weight:700;
    color: rgba(56, 34, 252, 0.671);
    box-shadow: 2px 2px 5px #d3d3d3;
    letter-spacing: 2px;
    border-radius: 3px;
    padding:5px 10px;
    position: relative;
    text-shadow:none;
}

.form-control .update{
    width:70%;
}

h1{
    font-size:26px;
}

.message{
    position: absolute;
    top: 10%;
    display:none;
    color: red;
    padding:5px;
    width: 100%;
}

.redirect{
    top: 20%;
}

.form-control form {
    margin: auto;
    margin-top: 30px;
}

.form-control form.login-form {
    margin-top:80px;
}

form label {
    font-size: 11px;
    font-weight: 550;
    color: rgb(82, 81, 81);
}

.form-group{
    display: flex;
    background-color: #fff;
    border-radius: 5px;
    padding: 5px;
    box-shadow: inset 1px 1px 5px rgb(177, 177, 177);
}

.form-group div{
    padding: 0 5px;
    font-size: 22px;
    color: rgb(78, 78, 78);
}


.form-group .fa{
    transition: 0.3s ease-in-out;
}


.form-group:hover .fa{
    color: rgb(92, 69, 148);

    transform: scale(1.1);
}

.form-group span{
    display: block;
    width: 2.5px;
    height: 20px;
    margin: auto 3px;
    color: rgb(78, 78, 78);
    background-color: rgb(78, 78, 78);
    transition: 0.3s ease-in-out;
}


.form-group:hover span{
    height: 30px;
}

.form-group input{
    width: 100%;
    font-size: 13px;
    letter-spacing:1px;
    font-weight:600;
    border: none;
    outline: none;
    padding: 5px;
    border-radius: 5px;
    color: rgb(105, 99, 99);
    transition: 0.3s ease-in-out;
}

.form-group input:focus{
    box-shadow: inset 1px 1px 5px rgb(177, 177, 177);

}

.form-group input[type="radio"]{
    width: 70%;
    display:none;
}
.genderMale input:checked + label .fa{
    color: rgb(78, 54, 184);
    transform: scale(1.3);
}

.genderFemale input:checked + label .fa{
    color: rgb(78, 54, 184);
    transform: scale(1.3);

}


.gender{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:30%;
}

button {
    text-align: center;
    align-items: center;
    padding: 12px 10px;
    width: 60%;
    border: none;
    outline: none;
    color: #e8e8e8;
    font-size: 15px;
    font-weight: 700;
    background:none;
    letter-spacing: 2px;
    background-color: hsla(199, 17%, 47%, 0.842);
    box-shadow: 3px 3px 12px rgb(179, 179, 179);
    border-radius: 3px;
    margin: 25px 20%;
    transition: 0.3s ease-in-out;
}


button:hover{
    background-color: hsla(200, 16%, 29%, 0.71);
    box-shadow: 8px 8px 15px #adadad, 
    -8px -8px 15px #ffffff;
}

.or-line {
    border-bottom: 2px solid rgb(206, 203, 203);
    text-align: center;
    width: 100%;
    height: 8px;
    margin: 10px 0px 10px 0px;
    position: relative;
    cursor: auto;
}

.or-line span {
    position: absolute;
    top: 0;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: -10px;
    background-color: white;
    font-weight: 550;
    font-size: 10px;
    color: grey;
}

.thirdPartyAuth{
    display:flex;
    flex-direction: column;
    margin-bottom: 20px;
}

.thirdPartyAuth h3{
    font-size: 15px;
    margin-top: 5%;
    color: rgb(73, 69, 69);
    font-size:13px;
    font-weight: 600;
}

.thirdParties{
    width:100%;
    height: 50px;
    margin-top: 5%;
    display: flex;
    justify-content:space-between;
}
.thirdParties>div{
    width: 80px;
    height: 50px;
    padding:14px 27px;
    background-color: #fff;
    border: 2px solid #f6f5fa;
    transition: all .3s;
}

.thirdParties>div:hover{
    border-color:#dddce0;
    box-shadow:0 4px 15px rgba(100,100,102,.6);
}

.thirdParties img{
    margin: 0;
    width: 100%;
}
form a {
    font-size: 15px;
    font-weight: 500;
    text-decoration: none;
    letter-spacing:1px;
}

span{
    color:#494949;
}
.login form{
    margin-top:120px;
}

.register-container{
    width: 930px;
    height: 800px;
}

.register-container .img-wrapper{
    padding: 6px 0px 0px 0px;
    width: 100%;
}
.register-container img{
    width: 100%;
    transform: scale(1.07)
}

@media all and (max-width:500px) {
    .container {
        margin-top: 30%;
        width: 90%;
        flex-direction: column;
        position: relative;
    }
}

@media all and (min-width:505px) and (max-width:1000px) {
    .container {
        width: 90%;
        flex-direction: column;
        position: relative;
    }
  

}